import request from '../../util/request'

export default {
    name: 'My',
    data () {
        return {
            shareModal: false,
            clipboard: '我正在用畅游看吧读免费百万小说。下载地址：git@github.com:songning123456/cykb-wx.git',
            displayInfo: [
                {
                    icon: 'appreciate',
                    title: '请给我好评'
                },
                {
                    icon: 'share',
                    title: '分享给朋友'
                },
                {
                    icon: 'comment',
                    title: '意见反馈'
                },
                {
                    icon: 'settings',
                    title: '设置'
                }
            ]
        }
    },
    computed: {
        UserInfo () {
            return this.$store.state.userInfo
        },
        sexClazz () {
            let clazz = ''
            if (this.$store.state.userInfo) {
                if (this.$store.state.userInfo.gender === 1) {
                    clazz = 'cuIcon-male bg-blue'
                } else if (this.$store.state.userInfo.gender === 2) {
                    clazz = 'cuIcon-female bg-pink'
                }
            }
            return clazz
        },
        avatarClazz () {
            let clazz = ''
            if (this.$store.state.userInfo && this.$store.state.userInfo.avatar) {
                clazz = `background-image:url(${this.$store.state.userInfo.avatar})`
            }
            return clazz
        }
    },
    methods: {
        tapBtn (type) {
            switch (type) {
                case 'appreciate':
                    break
                case 'share':
                    this.shareModal = true
                    break
                case 'comment':
                    uni.navigateTo({
                        url: '/pages/comment/Comment'
                    })
                    break
                case 'settings':
                    uni.navigateTo({
                        url: '/pages/settings/Settings'
                    })
                    break
            }
        },
        hideShareModal (type) {
            this.shareModal = false
            if (!type) {
                // 复制到剪切板
                uni.setClipboardData({
                    data: this.clipboard,
                    success: function () {
                        console.log('success')
                    }
                })
            }
        },
        wxBtn () {
            uni.login({
                success: response2 => {
                    // 获取用户信息
                    uni.getUserInfo({
                        provider: 'weixin',
                        success: response3 => {
                            uni.showLoading({
                                title: '登陆中',
                                mask: true
                            })
                            let params = {
                                condition: {
                                    code: response2.code,
                                    avatar: response3.userInfo.avatarUrl,
                                    nickName: response3.userInfo.nickName,
                                    gender: response3.userInfo.gender
                                }
                            }
                            request.post('/users/weixin/getUsersInfo', params).then(data => {
                                uni.hideLoading()
                                if (data.status === 200) {
                                    let result = JSON.stringify(data.data[0])
                                    uni.setStorage({
                                        key: 'userInfo',
                                        data: result
                                    })
                                    this.$store.commit('SET_USERINFO', data.data[0])
                                    this.$emit('userInfo', data.data[0])
                                } else {
                                    uni.showToast({
                                        title: '获取用户信息失败',
                                        duration: 1000,
                                        icon: 'none'
                                    })
                                }
                            }).catch(e => {
                                uni.hideLoading()
                                uni.showToast({
                                    title: '获取用户信息失败',
                                    duration: 1000,
                                    icon: 'none'
                                })
                            })
                        },
                        fail: reject3 => {
                            debugger;
                            // doNothing...
                        }
                    })
                },
                fail: response2 => {
                    debugger;
                    uni.showToast({
                        title: '登录失败',
                        duration: 1000,
                        icon: 'none'
                    })
                }
            })
        }
    }
}
